function compare(keyword, ...rest){
    var finded = false
    rest.map( (arg) => {
        if (arg && arg.toLowerCase().includes(keyword.toLowerCase())) finded = true
    })
   
    if (finded) return true 
    else return false
}

export const filter = (array, keyword, managerFilter = 0) => {
    var result = [];

    // Ð¿ÑÐ¸Ð¼ÐµÐ½Ð¸ÑÑ Ð¿Ð¾Ð¸ÑÐº Ð¿Ð¾ ÐºÐ»ÑÑÐµÐ²Ð¾Ð¼Ñ ÑÐ»Ð¾Ð²Ñ
    if (keyword !== '') {
        result = array.filter( (order) => compare(keyword, order.InvoiceNumber, order.Entity ))         
    }
    else result = array

     //Ð¿ÑÐ¸Ð¼ÐµÐ½Ð¸ÑÑ Ð¿Ð¾Ð¸ÑÐº Ð¿Ð¾ ÑÐ¸Ð»ÑÑÑ
    if (managerFilter !== 0){
        var newOrders = []
        result.map( (ord) => {
            if (ord.ManagerID === managerFilter) newOrders.push(ord)
        })
        return newOrders
    }
    else return result
}


export const search_alg = (array, keyword) => {
    return array.filter( (order) => compare( keyword, order.InvoiceNumber, order.Entity )) 
}