export const GetOrdersSubscription = gql`
subscription{
    erp_Orders(where: {OrderStatusID: {_in: [1,2]} }) {
      OrderID
      Entity
      InvoiceNumber
      City
      ShippingDate
      AcceptanceDate
      PaidAmount
      TotalAmount
      AwaitingDispatch
      NeedAttention
      CreatingDate
      ManagerID
      OrderStatus {
        Name
        ID
      }
      OrderItems(order_by: {OrderItemID: asc}){
        Quantity
        OrderItemID
        Name
        OrderID
      } 
      User {
        FirstName
        LastName
      }

    }  
  }
`
