import { Link } from 'react-router-dom'
import  moment  from 'moment';
import sass from '../main.module.sass'

const setPaidPercent = (total, paid) => {
    if (!total || !paid) return ' - 0%'
    return ' - ' + ((paid/total) * 100).toFixed(0) + '%'
}

